.table-box .table-header {
  background: #fff;
  height: 65px;
  line-height: 65px;
  border: 1px solid #ddd;
  border-bottom: 0;
  text-align: center;
  vertical-align: middle;
  color: #000;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}
.table-box .table-footer {
  background: rgba(255, 255, 255, 0);
  height: 52px;
  border: 1px solid #ddd;
  border-top: 0;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  margin-top: -52px;
}
.table-box table {
  font-size: 14px;
  border-spacing: 0;
  border-collapse: collapse;
  text-align: center;
  width: 100%;
  max-width: 888px;
  color: #000;
}
.table-box table thead {
  font-size: 14px;
  color: #000;
  background-color: #f3f3f3;
}
.table-box table thead tr td {
  border: 1px solid #ddd;
  padding: 8px;
  vertical-align: middle;
}
.table-box table tbody {
  background-color: #fff;
  border: 1px solid #ddd;
}
.table-box table tbody tr {
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: middle;
  height: 52px;
}
.table-box table tbody tr td {
  border: 1px solid #ddd;
  padding: 8px;
  line-height: 1.42857143;
  vertical-align: middle;
  border-bottom: 0;
}
.table-box table tbody tr td:last-child {
  border-right: 0;
}
.table-box table tbody tr td:first-child {
  border-left: 0;
}
.table-box table tbody:last-child {
  border: 0;
}
.table-box table .button-del {
  font-size: 12px;
  line-height: 1.5;
  background: #fff;
  color: #91969b;
  padding: 6px 12px;
  cursor: pointer;
  border: 1px solid #bdbcbc;
  border-radius: 4px;
  margin-left: 10px;
}
.table-box table .button-del:hover {
  background: #ff3f3f;
  border: 1px solid #ff2e2e;
}
