.maintitlewithhelp {
  width: 690px;
  overflow: hidden;
  margin-top: 8px;
  font-size: 18px;
  padding-bottom: 16px;
  border-bottom: #E9E9E9 solid 1px;
}
.maintitlewithhelp .help_text {
  font-size: 14px;
  line-height: 150%;
}
.maintitlewithhelp .page-help {
  color: #007DFF;
  font-size: 16px;
  padding: 16px 0 10px 0;
  cursor: pointer;
}
.maintitlewithhelp .page_help_title {
  display: inline;
  margin-left: 5px;
}
