.form-label-input .form-lable {
  display: inline-block;
  font-size: 14px;
  width: 170px;
  margin-top: 8px;
  margin-right: 10px;
  color: #000;
}
.form-label-input .form-input {
  font-size: 14px;
  padding: 0 15px;
  border: 1px;
  border-radius: 25px;
  width: 330px;
  height: 30px;
  background: #f3f3f3;
  background-color: #f3f3f3;
}
.form-label-input .form-input:hover,
.form-label-input .form-input:focus {
  border: 1px solid #007DFF;
  border-radius: 25px;
  width: 330px;
  background: #f3f3f3;
  background-color: #f3f3f3;
  outline: none;
}
.form-label-input .form-tip-err {
  color: red;
  margin-left: 185px;
  font-size: 14px;
}
.form-label-input .form-tip {
  color: #585454;
  margin-left: 185px;
  font-size: 14px;
}
.form-label-input .password-eye {
  margin-left: -32px;
  margin-top: 7px;
}
.form-label-input .form-select {
  cursor: pointer;
  font-size: 14px;
  padding: 0 15px;
  border: 1px;
  border-radius: 25px;
  width: 330px;
  height: 30px;
  background: #f3f3f3;
  background-color: #f3f3f3;
}
.form-label-input .form-select:hover,
.form-label-input .form-select:focus {
  border: 1px solid #007DFF;
  border-radius: 25px;
  width: 330px;
  height: 30px;
  background: #f3f3f3;
  background-color: #f3f3f3;
  outline: none;
}
.form-label-input .form-button {
  cursor: pointer;
  color: #007DFF;
  font-size: 16px;
  padding: 0 15px;
  border: 1px;
  border-radius: 25px;
  width: 330px;
  height: 40px;
  margin-top: 8px;
  border: 1px solid #e0e0e0;
  background: #f7f7f7;
  background-color: #f7f7f7;
  outline: none;
}
.form-label-input .form-button:hover {
  border-radius: 25px;
  width: 330px;
  background: #f3f3f3;
  background-color: #f3f3f3;
}
.form-label-input .form-button:disabled {
  color: #ACD1F9;
}
.login-input {
  font-size: 16px;
  padding: 0 15px;
  border: 1px;
  border-radius: 25px;
  width: 300px;
  height: 40px;
  background: #f3f3f3;
  background-color: #f3f3f3;
}
.login-input:hover,
.login-input:focus {
  border: 1px solid #007DFF;
  border-radius: 25px;
  width: 300px;
  background: #f3f3f3;
  background-color: #f3f3f3;
  outline: none;
}
.login-button {
  cursor: pointer;
  color: #fff;
  font-size: 16px;
  padding: 0 15px;
  border: 1px;
  border-radius: 25px;
  width: 300px;
  height: 40px;
  margin-top: 8px;
  border: 1px solid #e0e0e0;
  background: #007dff;
  background-color: #007dff;
  outline: none;
}
.login-button:hover {
  border-radius: 25px;
  width: 300px;
  background: #0261c4;
  background-color: #0261c4;
}
