.my-madal .modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1213;
  overflow: scroll;
  /*.dialog {
            background: #FFFFFF;
            min-width: 340px;
            padding: 20px 16px;
            margin-top: 60px;
            text-align: center;
            border-radius: 8px;
            box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, .1);
        }*/
}
.my-madal .modal-content {
  background: #FFFFFF;
  padding: 16px 32px;
  margin: 8px auto;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.1);
  border-radius: 12px;
  color: #454545;
  font-size: 12px;
  /*width: 580px;*/
  min-width: 300px;
  top: 200px;
  align-items: center;
  position: absolute;
  min-height: 100px;
}
.my-madal .title {
  border-bottom: #E9E9E9 solid 1px;
  font-size: 16px;
  color: #000;
  text-align: center;
  line-height: 45px;
}
.my-madal .close {
  float: right;
  height: 22px;
  width: 22px;
  border-radius: 50%;
  border: 2px solid #a3a3a3;
  text-align: center;
  cursor: pointer;
  margin: 0 10px 10px 10px;
}
@media screen and (max-width: 650px) {
  .my-madal .modal-content {
    min-width: 300px;
    width: 96%;
    max-width: 105%;
  }
}
