.login_win {
  height: 725px;
  width: 100%;
  margin: 0px auto;
  z-index: 10;
  display: block;
  background-color: #007DFF;
}
.login_win .login-border {
  margin: 0 auto;
  padding-top: 140px;
  outline: none;
  border-style: none;
  position: relative;
  z-index: 20;
}
.login_win .login-border .login-content {
  width: 630px;
  height: 420px;
  margin: 0px auto;
  padding: 15px 0;
  background-color: #fff;
  border-radius: 12px;
}
.login_win .login-border .login-content .content-left {
  padding-top: 18px;
  margin: 0 auto;
  width: 364px;
  float: left;
  text-align: center;
}
.login_win .login-border .login-content .content-left .login_title {
  font-size: 18px;
  color: #000;
  padding-bottom: 30px;
}
.login_win .login-border .login-content .content-left .login-form-tip {
  text-align: left;
  margin-left: 30px;
  padding: 10px;
}
.login_win .login-border .login-content .content-left .forget-password {
  color: #007DFF;
  padding: 10px;
}
.login_win .login-border .login-content .content-right {
  padding-top: 80px;
  width: 266px;
  font-size: 14px;
  float: left;
}
.login_win .login-border .login-content .content-right .right-border {
  border-left: 1px solid #CCCCCC;
}
.login_win .login-border .login-content .content-right .right-border .qr-msg {
  width: 150px;
  margin: 10px auto;
  text-align: center;
  color: #000;
}
.page_footer {
  height: 80px;
  background-color: #F3F3F3;
  width: 100%;
  min-width: 1350px;
}
.page_footer .footer_maintent {
  width: 1150px;
  height: 50px;
  margin: 0 auto;
  background-color: #F3F3F3;
}
